import React, { useState, useEffect, useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import axios from 'axios';
import Nav from '../components/Nav';
import '../styles/chat.css';

export default function Chat() {
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);
  const id = window.location.pathname.split("/")[2];
  const endOfMessagesRef = useRef(null); // Reference to the end of messages

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (message !== "") {
        const newMessage = {
          receiver: id,
          text: message
        };

        const response = await axios.post("/api/chat", newMessage, { withCredentials: true });
        if (response.status === 200) {
          setMessage("");  // Clear the input after sending the message
          fetchMessages(); // Refresh the messages list
        }
      }
    } catch (error) {
      console.log(error);
    }
  };

  const fetchMessages = async () => {
    try {
      const response = await axios.get(`/api/chatting/${id}`, { withCredentials: true });
      if (response.status === 200) {
        const combinedMessages = [...response.data.you, ...response.data.friend];

        // Sort the messages by createdAt to maintain correct order
        const sortedMessages = combinedMessages.sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt));

        setMessages(sortedMessages);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    fetchMessages();
  });

  useEffect(() => {
    // Scroll to the bottom whenever messages change
    endOfMessagesRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  return (
    <>
      <div className="chat">
        <div className="messages">
          <ul className="messages-list">
            {messages.map((msg, index) => (
              <li className={`bubble ${msg.sender === id ? 'left' : 'right'}`} key={index}>
                <div className="message-content">{msg.message}</div>
                <div className="message-time">{new Date(msg.createdAt).toLocaleTimeString()}</div>
              </li>
            ))}
            <div ref={endOfMessagesRef} /> {/* Empty div to act as the scroll target */}
          </ul>
        </div>
        <div className="input">
          <form onSubmit={handleSubmit}>
            <input
              type="text"
              placeholder="Write a message"
              value={message}
              onChange={(e) => setMessage(e.target.value)}
            />
            <button type="submit">
              <FontAwesomeIcon icon="fas fa-paper-plane" />
            </button>
          </form>
        </div>
      </div>
      <Nav />
    </>
  );
}
