import React, { useState, useEffect } from 'react';
import axios from 'axios';
import '../styles/friends.css';
import Nav from './Nav';
import { toast, ToastContainer, Bounce } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useNavigate } from 'react-router-dom';

export default function Friends() {
  const navigate = useNavigate();
  const [friends, setFriends] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleclick = async (id) => {
    try{
      const response = await axios.get(`/api/chat/${id}`, { withCredentials: true });
      if (response.status === 200) {
        navigate(`/chatting/${id}`);
        
      }
    }catch(error){
      console.log(error);
    }
  };

  const fetchFriends = async () => {
    setLoading(true);
    toast.info('Loading friends...');
    try {
      const response = await axios.get("/api/friends", { withCredentials: true });
      if (response.status === 200) {
        setFriends(response.data);
      }
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
      toast.dismiss();
    }
  };

  useEffect(() => {
    fetchFriends();  // Charger les amis lors du premier rendu seulement
  }, []);  // Le tableau vide [] empche le useEffect de se dclencher plusieurs fois

  return (
    <>
      <h3>Choose your friend</h3>
      <div className="friends">
        {friends.map(friend => (
          <div className="friend" key={friend._id} onClick={() => handleclick(friend._id)}>
            {friend.fullname}
            <img src={friend.profilepic.url} alt={friend.fullname} />
          </div>
        ))}
      </div>
      <Nav />
      <ToastContainer/>
    </>
  );
}