import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { ToastContainer, toast, Bounce } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Nav from '../components/Nav';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart, faHeartBroken, faChevronDown, faChevronUp } from '@fortawesome/free-solid-svg-icons';
import home from "../styles/Home.module.css";

export default function Home() {
  const [data, setData] = useState([]);
  const [commentInput, setCommentInput] = useState('');
  const [showComments, setShowComments] = useState({}); // Track visibility of comments for each post

  const pending = () => {
    toast.info("Loading", {
      position: "top-center",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "dark",
      transition: Bounce,
    });
  };

  const fetchPosts = async () => {
    const t = pending();
    try {
      const res = await axios.get("/api/home", { withCredentials: true });
      toast.dismiss(t);
      setData(res.data.posts);
      // Initialize showComments state for each post
      const initialShowComments = res.data.posts.reduce((acc, post) => {
        acc[post._id] = false; // Hide comments by default
        return acc;
      }, {});
      setShowComments(initialShowComments);
    } catch (error) {
      console.error(error);
    }
  };

  const handleLikeToggle = async (id, isLiked) => {
    try {
      const endpoint = isLiked
        ? `/api/unlike/${id}`
        : `/api/like/${id}`;

      const response = await axios.patch(endpoint, {}, { withCredentials: true });

      if (response.status === 200) {
        toast.success(isLiked ? "Unliked" : "Liked", {
          position: "top-center",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "dark",
          transition: Bounce,
        });

        // Update likes count and the isLiked flag in the state
        setData(prevData =>
          prevData.map(post =>
            post._id === id
              ? {
                  ...post,
                  likes: isLiked ? post.likes - 1 : post.likes + 1,
                  isLiked: !isLiked,
                }
              : post
          )
        );
      }
    } catch (error) {
      console.error(error);
    }
  };

  // Handle comment submission
  const handleCommentSubmit = async (postId) => {
    try {
      const response = await axios.post(
        `/api/comment/${postId}`,
        { comment: commentInput }, // Send comment to backend
        { withCredentials: true }
      );
      if (response.status === 200) {
        console.log(response.data);
        setCommentInput(''); // Clear comment input field
        fetchPosts(); // Reload posts to show the new comment
      }
    } catch (error) {
      console.error(error);
    }
  };

  const toggleComments = (postId) => {
    setShowComments(prevState => ({
      ...prevState,
      [postId]: !prevState[postId]
    }));
  };

  useEffect(() => {
    fetchPosts();
  }, []);

  return (
    <div className={home.home}>
      <ToastContainer
        position="top-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="dark"
        transition={Bounce}
      />
      <h3>Home</h3>
      <div className={home.posts}>
        {data.map((post) => (
          <div className={home.post} key={post._id}>
            <div className={home.info}>
              <div className={home.user}>
                <img src={post.user.profilepic.url} alt="Profile" />
                <span>{post.user.fullname}</span>
              </div>
              <span className={home.date}>{new Date(post.date).toDateString()}</span>
            </div>
            <div className={home.content}>
              <p>{post.title}</p>
              <p>{post.body}</p>
              <img src={post.images.url} alt="Post" />
            </div>
            <div className={home.footer}>
              <div className={home.likes}>
                <FontAwesomeIcon
                  icon={post.isLiked ? faHeartBroken : faHeart}
                  onClick={() => handleLikeToggle(post._id, post.isLiked)}
                  style={{ cursor: 'pointer', color: post.isLiked ? 'red' : 'gray' }}
                />
                <span>{post.likes}</span>
              </div>
              <div className={home.comments}>
                <span>Comments</span>
                <button className={home.toggleComments} onClick={() => toggleComments(post._id)}>
                  <FontAwesomeIcon icon={showComments[post._id] ? faChevronUp : faChevronDown} />
                </button>
                {showComments[post._id] && (
                  <div className={home.commentSection}>
                    {post.comments.map((comment, idx) => (
                      <div key={idx} className={home.comment}>
                        <div className={home.commentUser}>
                          <img src={comment.user.profilepic.url} alt="Profile" />
                          <span>{comment.user.fullname}</span>
                        </div>
                        <p>{comment.body}</p>
                      </div>
                    ))}
                  </div>
                )}
                <div className={home.commentInput}>
                  <input
                    type="text"
                    value={commentInput}
                    onChange={(e) => setCommentInput(e.target.value)}
                    placeholder="Add a comment..."
                  />
                  <button onClick={() => handleCommentSubmit(post._id)}>Submit</button>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
      <Nav />
    </div>
  );
}
