import React from "react";
import { useState, useEffect } from "react";
import "../components/c-s/Followers.css"
import axios from "axios";

export default function Followers() {
  const [followers, setFollowers] = useState([]);

  const fetchFollowers = async () => {
    try {
      const reponse = await axios.get("/api/followers", {
        withCredentials: true,
      });

      if (reponse.status === 200) {
     // console.log(reponse.data);
        setFollowers(reponse.data);
      }
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    fetchFollowers();
  }, []);

  return (
    <div className="fol">
      <div className="f-content">
        <h3>Followers</h3>
        {followers.map((follower) => (
          <div className="follower" key={follower._id}>
            <img src={follower.profilepic.url} alt="" />
            <h3>{follower.fullname}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}
