import React, { useState, useEffect } from "react";
import axios from "axios";
import Nav from "../components/Nav";
import notif from "../styles/Notifications.module.css";

export default function Notifications() {
  const [notifications, setNotifications] = useState([]);
  const [accepted, setAccepted] = useState({}); // Store accepted statuses per notification

  const handleAccept = async (id) => {
    try {
      const res = await axios.post(
        `/api/accept/${id}`,
        {},
        { withCredentials: true }
      );
      if (res.data.mssg === "Already friends" || res.data.friend === "yes") {
        // Update the specific notification's accepted status
        setAccepted((prevAccepted) => ({
          ...prevAccepted,
          [id]: true,
        }));
      }
    } catch (error) {
      console.error("Error accepting friend request:", error);
    }
  };

  useEffect(() => {
    const fetchNotifications = async () => {
      try {
        const res = await axios.get("/api/notifications", {
          withCredentials: true,
        });
        setNotifications(res.data);
        // Initialize accepted status for each notification
        const initialAcceptedStatus = res.data.reduce((acc, notif) => {
          acc[notif.sender._id] = false; // Default to false (not accepted yet)
          return acc;
        }, {});
        setAccepted(initialAcceptedStatus);
      } catch (error) {
        console.error("Error fetching notifications:", error);
      }
    };
    fetchNotifications();
  });

  return (
    <>
      <div className={notif.notifications}>
        {notifications.length === 0 ? (
          <p>No notifications</p>
        ) : (
          <ul>
            {notifications.map((notif, index) => (
              <li key={index} className={notif.notif}>
                <img
                  src={notif.sender.profilepic.url || "default_image.png"}
                  alt="Notification"
                />
                <div className={notif.notifContent}>
                  {notif.type === "follow" && (
                    <div className={notif.follow}>
                      <h5>
                        {new Date(notif.createdAt).toDateString()}
                        &nbsp;&nbsp;&nbsp;
                        {new Date(notif.createdAt).toLocaleTimeString()}
                      </h5>
                      {notif.sender.fullname} sent you a Friend Request{" "}
                      {notif.isaccepted ? (
                        <p>Already friends</p>
                      ) : (
                        <button onClick={() => handleAccept(notif.sender._id)}>
                          Accept
                        </button>
                      )}
                    </div>
                  )}

                  {notif.type === "like" && (
                    <div className={notif.like}>
                      <h5>
                        {new Date(notif.createdAt).toDateString()}
                        &nbsp;&nbsp;&nbsp;
                        {new Date(notif.createdAt).toLocaleTimeString()}
                      </h5>
                      {notif.message}
                    </div>
                  )}
                  {notif.type === "comment" && (
                    <div className={notif.comment}>
                      <h5>
                        {new Date(notif.createdAt).toDateString()}
                        &nbsp;&nbsp;&nbsp;
                        {new Date(notif.createdAt).toLocaleTimeString()}
                      </h5>
                      {notif.message}
                    </div>
                  )}
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
      <Nav />
    </>
  );
}
