import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fas } from '@fortawesome/free-solid-svg-icons';
import axios from 'axios';
import Nav from '../components/Nav';

library.add(fas);

export default function FriendReq() {
  const [friend, setFriend] = useState("");
  const [message, setMessage] = useState(""); // State for showing messages (success/error)
  const [loading, setLoading] = useState(false); // State for loading indicator

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage(""); // Clear previous messages

    try {
      const response = await axios.post("/api/friendreq", { friend }, { withCredentials: true });

      if (response.status === 200) {
        setMessage(response.data.message); // Show success message
      }
    } catch (error) {
      // Handle different types of errors
      if (error.response) {
        // Server responded with a status other than 200 range
        setMessage(error.response.data.message || "An error occurred while sending the friend request.");
      } else if (error.request) {
        // Request was made but no response was received
        setMessage("No response from server. Please try again later.");
      } else {
        // Something happened in setting up the request
        setMessage("Error in sending request: " + error.message);
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className='friendreq'>
      <h1>Friend Requests</h1>
      <form onSubmit={handleSubmit}>
        <label htmlFor="sendreq">
          Send a Friend Request
        </label>
        <input
          type="text"
          name="sendreq"
          id="sendreq"
          placeholder='Enter Email'
          required={true}
          value={friend}
          onChange={(e) => setFriend(e.target.value)}
        />
        <button type="submit" disabled={loading}>
          {loading ? "Sending..." : <FontAwesomeIcon icon="fas fa-paper-plane" />}
        </button>
      </form>
      {message && <p>{message}</p>}
      <Nav />
    </div>
  );
}
