import React, { useState, useEffect } from "react";
import axios from "axios";
import "../styles/Profile.css";
import Nav from "../components/Nav";
import FriendReq from "./FriendReq";
import Followers from "../components/Followers";
export default function Profile() {
  const [pic, setPic] = useState(null);
  const [nposts, setNposts] = useState(0);
  const [nfriends, setNfriends] = useState(0);
  const [name, setName] = useState("");

  const [isActive, setIsActive] = useState(false);

  const handlefollowers = () => {
    //console.log("clicked");
    setIsActive(true);
  };
  const handleposts = () => {
      
      console.log("clicked");
  }
  const fetchProfile = async () => {
    try {
      const res = await axios.get("/api/profile", { withCredentials: true });
      setPic(res.data.pic);
      setNposts(res.data.nposts);
      setNfriends(res.data.nfriends);
      setName(res.data.name);
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    fetchProfile();
  });

  return (
    <div className="profile">
      {isActive ? <Followers/> : pic ? (
        <img src={pic} alt="Profile" className="profile-pic" />
      ) : (
        <p>Loading profile picture...</p>
      )}
      <h3>{name}</h3>
      <div className="profile-info">
        <a className="p" onClick={handleposts}><h3>{nposts} posts</h3></a>

        <a className="p" onClick={handlefollowers}>
          <h3>{nfriends} followers</h3>
        </a>
      </div>

      <div className="allposts">
        <div className="ps"></div>
      </div>
      <FriendReq></FriendReq>
      <Nav></Nav>
    </div>
  );
}
